import { arrayToObject } from '../utilities/convertData';
import postTypes from '../post-types';

const postTypeDefaultState = arrayToObject(postTypes);


const initialState = {
    width: null,
    data: {
		...postTypeDefaultState
	},
	menus: {},
	lists: {
		pages: []
	}
};

export const api = (state = initialState, action) => {
	switch (action.type) {
        case 'SET_WIDTH':
        return {
            ...state,
            width: action.width,
        }
		case 'LOAD_DATA':

			return {
				...state,
				data: {
					...state.data,
					[action.payload.type]: arrayToObject(action.payload.data, 'slug')
				}
			}

		case 'LOAD_DATA_BY_SLUG':

			return {
				...state,
				data: {
					...state.data,
					[action.payload.type]: {
						...state.data[action.payload.type],
						[action.payload.slug]: action.payload.data[0]
					}
				}
			};

		case 'LOAD_PAGES_LIST':
			return {
				...state,
				lists: {
					...state.lists,
					pages: action.payload
				}
			};

		case 'LOAD_MENU':

			return {
				...state,
				menus: {
					...state.menus,
					[action.payload.slug] : action.payload.menu
				}
			};

		case 'CLEAR_API_CONTENT':

			return {
				...initialState
			}

		case 'CLEAR_API_DATA_BY_SLUG':

			if (state.data[action.payload]) {
				let newState = { ...state};
				delete newState.data[action.payload];
				return newState;
			}

			return state;

		default:
			return state;
	}
};
export const setWidth = widthDOM => ({ type: 'SET_WIDTH', width: widthDOM });

export const setAsyncWidth = widthDOM => dispatch => (
    new Promise((resolve, reject) => {
        setTimeout(() => resolve(), 2000);
    })
        .then(() => dispatch(setWidth(widthDOM)))
);



